body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #222; 
  background: #f4f4f4; 
  font: 400 14px CoreSans, Arial, sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",monospace;
}

a { color: #222; 
}

a:hover { 
  text-decoration: underline; 
}

ul, li { 
  list-style: none; 
  padding: 0; margin: 0; 
}

input { padding: 10px; 
  border-radius: 5px; 
  outline: none; 
  margin-right: 10px; 
  border: 1px solid #dddddd; 
}

button { 
  padding: 10px; 
  border-radius: 5px; 
  border: 1px solid #dddddd;
  background: transparent; color: #808080; cursor: pointer;
}

button:hover { 
  color: #222; 
}

*:focus { 
  outline: none; 
}

.align-middle {
  vertical-align: middle;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.intro {
  color: #61dafb;
}

.page { margin: 20px; 
}
.interactions { text-align: center; 
}

.table { margin: 20px 0; 
}
.table-header { 
  display: flex; 
  line-height: 24px; 
  font-size: 16px; 
  padding: 0 10px; 
  justify-content: space-between; 
}
.table-empty { 
  margin: 200px; 
  text-align: center; 
  font-size: 16px; 
}
.table-row {
  display: flex; 
  line-height: 24px; white-space: nowrap; 
  margin: 10px 0; padding: 10px; 
  background: #ffffff; border: 1px solid #e3e3e3;
}
.table-header > span { 
  overflow: hidden; 
  text-overflow: ellipsis; 
  padding: 0 5px; 
}
.table-row > span { 
  overflow: hidden; 
  text-overflow: ellipsis; 
  padding: 0 5px; 
}

.button-inline { 
  border-width: 5; 
  background: transparent; 
  color: inherit; 
  text-align: inherit; 
  -webkit-font-smoothing: inherit; 
  padding: 0; font-size: inherit; 
  cursor: pointer; 
}
.button-active { 
  border-radius: 0; 
  border-bottom: 1px solid #38BB6C; 
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

